import React from 'react';

function Tile(props){
	return(
		<img src={props.source} style={{height: props.size, width: props.size}}/>
	);
}

export default class ImageTiles extends React.Component{
	render(){
		return(
			<div>
				<Tile source="./pics/tile1.jpg" size="200"/>
			</div>
		);
	}
}



